import {Component, OnInit} from '@angular/core';
import {NgForm} from "@angular/forms";
import {DataServicesService} from "../services/data-services.service";
import {UsersDTO} from "../services/usersDTO";
import {Message} from "primeng/primeng";

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.css'],
  providers: [DataServicesService]
})
export class SignUPComponent implements OnInit {
  userDTO: UsersDTO = {};
  msgs: Message[] = [];

  constructor(public DS: DataServicesService) {
  }

  ngOnInit() {
  }

  onSignUp(data: NgForm) {
    if (data.invalid) {
      return;
    }
    this.userDTO.email = this.userDTO.userName;
    this.DS.signUp(this.userDTO).subscribe((res) => {
      if (res) {
        if (res.success) {
          this.msgs = [{severity:'success', summary:'Success Message', detail:res.message}];
        } else {
          this.msgs = [{severity:'error', summary:'Error Message', detail: res.message}];
        }
      }
    });
  }
}



